@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

.spinner-container {
  position: absolute;
}

.spinner {
  width: 100px;
  height: 100px;
  border: 6px solid #df0e0e;
  border-top: 3px solid transparent;
  border-radius: 50%;
  animation: spin 0.8s linear 0s infinite;
  z-index: 2;
  top: 250px;
  position: relative;
}

.App {
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.title {
  color: #fff;
  font-size: 2.1rem;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
}